      ],
      tempCount: 0,

      cardDragOffset: 0
    };
  },
  created() {
    window.addEventListener("resize", this.resizeHandler);
  },
  mounted() {
    this.windowHeight = window.innerHeight;
    this.resetThis++;
    this.updateStack();
  },
  destroyed() {
    window.removeEventListener("resize", this.resizeHandler);
  },
  methods: {
    messageFocused() {
      document.body.classList.add("enableAndroidKeyboard");
    },
    messageBlurred() {
      document.body.classList.remove("enableAndroidKeyboard");
      window.scrollTo(0, 0);
    },
    sendMessage() {
      alert("Sending Message :p");
    },
    resizeHandler() {
      debounce(() => {
        this.windowHeight = window.innerHeight;
      }, 200)();
    },
    moving(e) {
      this.cardDragOffset = e.x;
    },
    resetMoving() {
      this.cardDragOffset = 0;
    },
    draggedRight() {
      this.resetCard();
      console.log("right");
    },
    draggedLeft() {
      this.resetCard();
      console.log("left");
    },
    resetCard() {
      this.cardDragOffset = 0;
      setTimeout(() => {
        this.resetThis++;
        this.$nextTick(() => {
          this.$refs.cardStack.classList.add("animateStackUp");
        });
        this.updateStack();
      }, 200);
    },
    updateStack() {
      this.tempCount = this.nextCount;
    }
  },
  computed: {
    nextCount() {
      return (this.tempCount + 1) % this.tempLipsum.length;
    },
    title() {
      return this.tempLipsum[this.tempCount].title;
    },
    nextTitle() {
      return this.tempLipsum[this.nextCount].title;
    },
    text() {
      return this.tempLipsum[this.tempCount].text;
    },
    nextText() {
      return this.tempLipsum[this.nextCount].text;
    },
    hasBg() {
      if (this.tempLipsum[this.tempCount].bg) return true;
      else return false;
    },
    hasBgNext() {
      if (this.tempLipsum[this.nextCount].bg) return true;
      else return false;
    },
    bg() {
      return this.tempLipsum[this.tempCount].bg;
    },
    bgNext() {
      return this.tempLipsum[this.nextCount].bg;
    },
    overlayOpacity() {
      let percent = (this.cardDragOffset / window.innerWidth) * 100;
      percent = Math.pow(1.137, Math.abs(percent));
      if (percent > 94) percent = 94;
      return percent / 100;
    },
    accepting() {
      return this.cardDragOffset > 0;
    },
    rejecting() {
      return this.cardDragOffset < 0;
    }
  }
};
</script>

